
const Notification = ( { message } ) => {

    console.log(message);

    if ( message.msg === null) {
        return null
    }

    return(
        <>
                {
                    message.msg === 'ok' ? 
                        <div className="success">
                        { message.content }
                        </div>
                    :
                    <div className="error">
                    { message.content }
                    </div>
                }
            
        </>
    )


}

export default Notification;