
const Filter = ( { persons, setPersons, phonebookService} ) => {
 
    const handleFilter = (e) => {
        const inputFilter = e.target.value;

        phonebookService
          .getAll()
          .then( initialState => { 
            setPersons( initialState.filter( p => p.name.includes(inputFilter) ) ) 
          });

        //const personsFilter = persons.filter( p => p.name.includes(inputFilter) );
        //setPersons(personsFilter);
        
    }

    return(
     <div>
        filter shown with <input onChange={handleFilter} />
      </div>
    )

}

export default Filter;